export function addDragons(main, Dragon) {
  const ul = document.createElement("ul");
  let li, content, ids, dRelation, notes;
  const { relationships, forces, dragons } = Dragon;

  for (const { id, name } of dragons.names) {
    li = document.createElement("li");
    content = document.createTextNode(name);
    li.appendChild(content);
    // relations
    ids = relationships.find( relation => relation.id === id )?.relations;
    notes =  forces.find( f => f.id === id )?.notes;

    if(notes){
        let p = document.createElement('p');
        p.appendChild(document.createTextNode( `Moyenne forces :  ${average(notes)}` ) );
        li.appendChild(p);
    }

    if(ids){
        dRelation = dragons.names.filter( dragon => ids.includes( dragon.id ) );
        let ulR = document.createElement("ul");
        let liR = null;
        for(const { name: nameR } of dRelation){
            liR = document.createElement('li');
            liR.appendChild(document.createTextNode(nameR));
            ulR.appendChild(liR);
        }

        li.appendChild(ulR);
    }

    ul.appendChild(li);
  }

  main.appendChild(ul);
}

const average = data =>  {  
    if(data.length > 0 ) 
        return Math.floor( (data.reduce((acc, curr) => acc + curr ) / data.length ) * 100) / 100 ;

    return null;
}